* {
  font-family: "proxima-nova", sans-serif;
}

#root {
  height: 100%;
}

body {
  height: 100vh;
}

.hide-mobile {
  display: block;
}
.show-mobile {
  display: none;
}
@media (max-width: 676px) {
  .hide-mobile {
    display: none;
  }
  .show-mobile {
    display: block;
  }
}

.container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

/* Mobile Top Bar */
.top-bar {
  position: fixed;
  background: #fff;
  height: 50px;
  border-bottom: 1px solid #ddd;
  width: 100%;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 2;
}

body {
  background: #faf9f9;
}

/* Global Feed / Team Feed Toggle */
.toggle-menu {
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: row;
      justify-content: row;
  padding: 0 15px;
  background: #fff;
}

.toggle-menu a {
  color: #000;
  text-decoration: none;
  display: block;
  margin-right: 20px;
}
.toggle-menu a span {
  display: block;
  margin-top: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}

@media (max-width: 676px) {
  .toggle-menu {
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0;
  }
  .toggle-menu a:first-of-type span {
    border-right: 1px solid #ddd;
  }
  .toggle-menu a {
    width: 50%;
    height: 100%;
    margin-right: 0px;
    text-align: center;
  }
}

.feed-title {
  background: none;
}

.feed-title {
  background: #fff;
}

/* Desktop Navbar */
.desktop-nav {
  background: #fff;
  height: 50px;
  border-bottom: 1px solid #ddd;
}
.desktop-nav > div {
  max-width: 800px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.desktop-nav .logo {
  width: 100px;
  padding: 14px;
}
.desktop-nav .menu {
  display: -ms-flexbox;
  display: flex;
}
.desktop-nav .menu a {
  padding: 14px;
  margin-right: 10px;
  color: #000;
  display: inline-block;
  text-decoration: none;
}

/* Mobile Navbar */
.mobile-nav {
  position: fixed;
  height: 50px;
  width: 100%;
  bottom: 0;
  left: 0;
  background: #fff;
  border-top: 1px solid #ddd;
}
.mobile-nav > div {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.mobile-nav a {
  display: block;
  color: #000;
  text-align: center;
  padding: 12px 18px;
}

